import $ from 'jquery'
import fetch from 'node-fetch'
let Parser = require('rss-parser');
let parser = new Parser();

const starred = $('.starred')
const lastrepos = $('.lastrepos')
const news = $('.news')
const tools = $('.tools')
const contact = $('.contact')
const about = $('.about')

async function getStarred() {
  let res = await fetch('https://api.github.com/users/injecti0n/starred')
  let data = res.json()
  return data
}

async function getRepos() {
  let res = await fetch('https://api.github.com/users/injecti0n/repos?per_page=1000')
  let data = res.json()
  return data
}


async function getBlogNews() {
  let res = await fetch('https://www.hack4.net/feeds/posts/default?alt=json')
  let data = res.json()
  return data
}

getBlogNews().then(res => {
  console.log(res.feed.entry)
  // console.log(res.feed.entry.title) // title
  res.feed.entry.forEach(news => {
    console.log(news.title.$t)
    document.querySelector('.newslist').innerHTML += `
   <li class="feed-item">
      <span class="text">${news.title.$t} <a href="#">${news.title.$t}</a></span>
   </li>
   `
    // news.published timeeeee
  })
})


getStarred().then(res => {
  console.log(res)
  res.forEach(repo => {
    document.querySelector('.starredlist').innerHTML += `
    <li class="feed-item">
                <span class="text">${repo.description} <a href="${repo.html_url}">${repo.html_url}</a></span>
              </li>
    `
  })

})

getRepos().then(res => {
  console.log(res)
  res.forEach(repo => {
    document.querySelector('.repos').innerHTML += `
    <li class="feed-item">
                <span class="text">${repo.description} <a href="${repo.html_url}">${repo.html_url}</a></span>
              </li>
    `
  })

})


function makeWindow(window_title, html_mount) {
  return new WinBox(window_title, {
    mount: document.getElementById(html_mount)
      .cloneNode(true),
    x: "center",
    y: "center",
    background: 'rgb(54 69 68)'
  });
}

function makeWindowIframe(window_title,url) {
  return new WinBox(window_title, {
    x: "center",
    y: "center",
    background: 'rgb(54 69 68)',
    url:url
  });
}

function makeWindowWithStatic(window_title,content) {
  return new WinBox(window_title, {
    x: "center",
    y: "center",
    background: 'rgb(54 69 68)',
    html:content
  });
}

starred.on('click', () => {
  console.log('starred')

  new WinBox("visitor@injecti0n:~$ cd repositories/starred/", {
    mount: document.getElementById("content")
      .cloneNode(true),
    x: "center",
    y: "center",
    background: 'rgb(54 69 68)'
  });

})

lastrepos.on('click', () => {
  console.log('lastrepos')

  makeWindow("visitor@injecti0n:~ repositories/my_repositories/ $", "latestrepos")

})

news.on('click', () => {
  console.log('news')

  makeWindow("visitor@injecti0n:~ hack4.net/news/ $", "hack4net")

})

tools.on('click', () => {
  console.log('tools')

  makeWindowIframe("visitor@injecti0n:~/internet $", "https://www.hack4.net/search/label/pentest%20tool")

})

contact.on('click', () => {
  console.log('contact')

  makeWindow("visitor@injecti0n:~/contact $ ", "content")

})


about.on('click', () => {
  console.log('about')

  makeWindowWithStatic("visitor@injecti0n:~/about $ ", "content")

})